.container{color:#fff;min-height:100vh;padding-top:64px;padding-inline:1rem;display:flex;flex-direction:column;overflow-x:hidden}.header{width:100%}.videoWrapper{position:absolute;top:0;left:0;width:100%;height:400px;overflow:hidden;z-index:-1}.videoWrapper::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:250px;background:linear-gradient(to bottom, transparent, black);pointer-events:none;z-index:2}.videoBackground{width:100%;height:100%;object-fit:cover;pointer-events:none;opacity:.4}.aboutSection{text-align:center;max-width:1000px;margin:4rem auto 3rem;display:flex;flex-direction:column;gap:1rem}.aboutSection h2{font-size:2rem;font-weight:600;color:#f0c332}.aboutSection p{font-size:1.8rem;font-weight:400}.aboutMeBlock{display:flex;max-width:750px;margin:0 auto;padding-bottom:5rem;align-items:flex-start;gap:4rem}@media(max-width: 768px){.aboutMeBlock{flex-direction:column;gap:2rem}}.aboutMeBlock .right{flex:1;display:flex;flex-direction:column}.aboutMeBlock .right p{font-size:1.2rem;line-height:1.7;color:#ccc;text-align:center;margin-bottom:.6rem}.aboutMeBlock .right ul{list-style-type:disc;padding-left:1.5rem;display:flex;flex-direction:column;gap:.4rem}.aboutMeBlock .right ul li{line-height:1.6}.aboutMeBlock h2{font-size:2rem;font-weight:600;color:#f0c332}.processSection{display:flex;flex-direction:column;gap:6rem;max-width:1000px;margin:0 auto;padding-bottom:6rem}.step{position:relative;min-height:220px;display:flex;flex-direction:column;gap:1rem}.leftRight{display:flex;align-items:center;justify-content:space-between;gap:2rem;opacity:0;transform:translateY(40px);will-change:transform,opacity}@media(max-width: 768px){.leftRight{flex-direction:column}}.left{flex:1}.left .stepNumber{font-size:1.2rem;font-weight:bold;color:#888}.left .stepTitle{position:relative;font-size:1.8rem;font-weight:600;color:#f0c332;z-index:1;padding-left:.2rem}.left .stepTitle::before{content:attr(data-step);position:absolute;top:-2.6rem;left:0;transform:translateX(-1.5rem);font-size:5rem;font-weight:800;color:#fff;opacity:.2;z-index:-1;pointer-events:none;user-select:none;line-height:1}.left .stepDescription{margin-top:.8rem;color:#bbb;line-height:1.6;font-size:1.05rem}.right{flex:1}.right .toolGrid{display:flex;flex-wrap:wrap;gap:20px 24px;align-items:center;justify-content:flex-start;margin-top:24px;margin-left:8px}@media(max-width: 768px){.right .toolGrid{justify-content:center;gap:20px;margin-left:0}}.right .toolLogo{width:140px;height:140px;display:flex;align-items:center;justify-content:center;padding:8px}@media(max-width: 768px){.right .toolLogo{width:100px;height:100px;padding:4px}}.middle{display:flex;flex-direction:column;align-items:center;position:absolute;top:0;left:50%;transform:translateX(-50%);height:100%;pointer-events:none}.middle .dot{width:14px;height:14px;border-radius:50%;background-color:rgba(0,0,0,0);border:2px solid hsla(0,0%,100%,.4);transition:background-color .3s ease,border-color .3s ease}.middle .dot.active{background-color:#fff;border-color:#fff}.middle .line{flex-grow:1;width:2px;background-color:hsla(0,0%,100%,.2);margin-top:.5rem}@media(max-width: 768px){.middle{display:none}}.deliverySection{max-width:1000px;margin:0 auto;padding-bottom:6rem;display:flex;flex-direction:column;align-items:center;text-align:center;gap:2rem;padding-inline:1rem}.deliveryTitle{font-size:2rem;font-weight:600;color:#f0c332}.deliveryIntro{font-size:1.1rem;color:#ccc;max-width:720px;line-height:1.6}.deliveryGrid{display:grid;grid-template-columns:repeat(3, 1fr);gap:2rem;width:100%;margin-top:2rem}.deliveryCard{background-color:#111;border:1px solid hsla(0,0%,100%,.1);border-radius:12px;padding:1.5rem;text-align:left;transition:transform .3s ease,box-shadow .3s ease}.deliveryCard:hover{transform:translateY(-4px);box-shadow:0 8px 20px rgba(0,0,0,.3)}.deliveryCard h4{font-size:1.2rem;font-weight:600;margin-bottom:.6rem}.deliveryCard p{font-size:1rem;color:#aaa;line-height:1.5}